.menu-main {
  padding-top: 3.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  grid-row-gap: 5rem;
  animation: rollout 0.4s;
}

.menu-meal-time-title {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
}

@keyframes rollout {
  0% { transform: translateY(100px); }
  100% { transform: none; }
}

