.meal-plans-main {
  margin-top: 20px;
  margin-bottom: 32px; 
  padding-top: 3.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  align-items: stretch;
  grid-row-gap: 5rem;
}

/* iPhone 13 Pro Max and bigger */
@media (max-width: 500px) {
  .meal-plans-main {
    grid-template-columns: 1fr;
  }
}