.cart-header {
  padding-top: 0.5rem;
}

.orders {
  margin: 2rem 0 0 0;
  min-height: calc(100vh - 15rem);
}

.orders-heading,
.orders-total-description-item,
.orders-total-description,
.orders-total {
  width: 100%;
  max-width: 600px;
}

.orders-heading {
  margin-bottom: 5.5rem;
  border-bottom: 2px solid #000;
}

.orders-total {
  text-align: right;
  border-top: 2px solid #000;
  margin-left: auto;
  margin-top: 1rem;
  font-weight: 700;
  font-size: 16pt;
}

.orders-total-description {
  text-align: right;
  border-top: 2px solid #000;
  margin-left: auto;
  margin-top: 3rem;
}

.orders-total-description-item {
  text-align: right;
  margin-left: auto;
  margin-top: 0.1rem;
  font-weight: 600;
}

.order-description-item-price {
  padding-left: 10px;
}

.order-description-delivery-price {
  padding-left: 20px;
}