.mokka-footer {
  border-top: 1px solid #000;
  margin-top: 3rem;
  padding: 2rem 0 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

footer div p {
  font-weight: 500;
  max-width: 600px;
  margin-right: 16px;
}



.credits-footer {
  margin-bottom: 32px;
  text-align: center;
}

.credits-footer a {
  color: black;
  font-weight: 500;
  font-variant-caps: all-petite-caps;
  font-size: 10pt;
}



.recyclable-div {
  display: flex;
  justify-content: center;
  align-items: baseline;
}

.recyclable-div p {
  font-size: 16pt;
  font-weight: 500;
}

.recyclable-div img {
  width: 35px;
  height: auto;
  margin-left: 4px;
}



.item-heart {
  font-size: 1.4rem;
}

.item-thank-you {
  font-size: 1.2rem;
}


/* iPhone 13 Pro Max and bigger */
@media (max-width: 500px) {
  .mokka-footer p {
    font-size: 0.9rem;
  }

  .item-thank-you {
    font-size: 1.1rem;
  }

  .item-heart {
    font-size: 1.1rem;
  }

  .recyclable-div img {
    margin-left: 0;
  }
}

/* iPhone 8 */
@media (max-width: 375px) {
  .recyclable-div p {
    font-size: 14pt;
  }  
}

/* iPhone SE */
@media (max-width: 320px) {
  .recyclable-div p {
    font-size: 13pt;
  }  
}