.what-we-serve-div-about-us {
    text-align: center;
}

.what-we-serve-div-about-us h1 {
    margin-top: 16px;
    margin-bottom: 32px;
    font-size: 32pt;
}



.what-we-serve-images-div-about-us {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.what-we-serve-images-div-about-us div {
    width: 50%;
    padding-left: 5%;
    padding-right: 5%;
    text-align: center;
    margin-top: 16px;
}

.what-we-serve-images-div-about-us div img {
    max-width: 100%;
    height: 200px;
    margin-bottom: 16px;
}

.what-we-serve-images-div-about-us div p {
    font-weight: 600;
    font-size: 18pt;
}

.what-we-serve-images-div-about-us .small {
    font-size: 12pt;
}



.main-div-about-us {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 32px;
    margin-bottom: 64px;
}

.main-div-about-us div {
    margin-top: 16px;
    margin-bottom: 16px;
    display: flex;
    flex-direction: row;
}

.main-div-about-us img {
    max-width: 100%;
    max-height: 400px;
}

.main-div-about-us .left-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: right;
}

.main-div-about-us .right-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
}

.main-div-about-us .left-item p {
    padding-left: 84px;
}

.main-div-about-us .right-item p {
    padding-right: 84px;
}

.main-div-about-us p {
    font-size: 22pt;
    line-height: 24pt;
    margin-bottom: 64px;
}

.main-div-about-us .small-left, .main-div-about-us .small-right {
    font-size: 14pt;
    line-height: 16pt;
    margin-bottom: 0;
}

.main-div-about-us .small-left {
    text-align: left;
}

.main-div-about-us .small-right {
    text-align: right;
}

.mobile-small {
    padding-left: 6px;
    padding-right: 6px;
    text-align: center;
    visibility: hidden;
    height: 0;
}

.lets-get-started h1 {
    font-size: 32pt;
    margin-top: 32px;
    margin-bottom: 0;
}

.delivery-info-div {
    margin-top: 32px;
    margin-bottom: 128px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.delivery-info {
    font-size: 13pt;
    text-align: center;
}

/* iPhone 13 Pro Max and bigger */
@media (max-width: 500px) {
    .what-we-serve-div-about-us {
        margin-top: 64px;
    }

    .what-we-serve-images-div-about-us div {
        padding-left: 2%;
        padding-right: 2%;
    }

    .what-we-serve-images-div-about-us div p {
        font-size: 14pt;
        margin-bottom: 4px;
    }

    .what-we-serve-images-div-about-us div img {
        height: 100px;
    }

    .what-we-serve-images-div-about-us .small {
        font-size: 11pt;
    }

    .what-we-serve-div-about-us h1 {
        font-size: 24pt;
    }

    .main-div-about-us .left-item p {
        padding-left: 0px;
    }
    
    .main-div-about-us .right-item p {
        padding-right: 0px;
    }

    .main-div-about-us .small-left, .main-div-about-us .small-right {
        visibility: hidden;
        height: 0;
    }

    .mobile-small {
        visibility: visible;
        height: auto;
    }

    .main-div-about-us img {
        height: 200px;
    }

    .main-div-about-us p {
        font-size: 14pt;
        line-height: 18pt;
    }

    .lets-get-started h1 {
        font-size: 24pt;
    }
}

/* iPhone 8 */
@media (max-width: 375px) {
    .what-we-serve-images-div-about-us div p {
        font-size: 13pt;
    }

    .what-we-serve-images-div-about-us .small {
        font-size: 10pt;
    }

    .what-we-serve-images-div-about-us div img {
        height: 100px;
    }

    .what-we-serve-div-about-us h1 {
        font-size: 22pt;
    }

    .main-div-about-us img {
        height: 190px;
    }

    .main-div-about-us p {
        font-size: 13pt;
        line-height: 17pt;
    }
}

/* iPhone SE */
@media (max-width: 320px) {
    .what-we-serve-images-div-about-us div p {
        font-size: 12pt;
    }

    .what-we-serve-images-div-about-us .small {
        font-size: 9pt;
    }

    .main-div-about-us img {
        height: 180px;
    }

    .main-div-about-us p {
        font-size: 12pt;
        line-height: 16pt;
    }
}