.item {
  width: 275px;
  padding: 0 0.8rem 0.8rem 0.8rem;
  background-color: #fff;
  border: 1px;
  border-radius: 30px;
  border-color: rgb(235, 235, 235);
  border-style: solid;
  -webkit-box-shadow: 6px 6px 16px 6px rgba(0,0,0,0.01);
  -moz-box-shadow: 6px 6px 16px 6px rgba(0,0,0,0.01);
  box-shadow: 6px 6px 16px 6px rgba(0,0,0,0.01);
  margin-right: 16px;
  text-align: center;
  flex-direction: column;
  display: flex;
  transition: 0.3s;
}

.item:hover {
  -webkit-box-shadow: 6px 6px 16px 6px rgba(0,0,0,0.10);
  -moz-box-shadow: 6px 6px 16px 6px rgba(0,0,0,0.10);
  box-shadow: 6px 6px 16px 6px rgba(0,0,0,0.10);
  border-color: white;
}

.item img {
  margin-top: -3.5rem;
  width: 220px;
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  border-radius: 10%;
  cursor: pointer;
}



.item-head_desc p {
  margin: 0;
  margin-bottom: 0.5rem;
}

.item-options-div {
  margin-top: 0;
  margin-bottom: 0.5rem;
  margin-left: -0.1rem;
  margin-right: auto;
}


*,
*::before,
*::after {
  box-sizing: border-box;
}

select {
  appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  outline: none;
}

:root {
  --select-border: #777;
  --select-focus: blue;
  --select-arrow: var(--select-border);
}

.option-list-div {
  /* width: 70%; */
  /* margin-right: 0; */
  min-width: 20px;
  border: 1px solid var(--select-border);
  border-radius: 1em;
  padding: 0.25em 0.5em;
  font-size: 0.9rem;
  cursor: pointer;
  line-height: 1.0;
  background-color: #fff;
  display: grid;
  grid-template-areas: "select";
  align-items: center;
}

select,
.option-list-div:after {
  grid-area: select;
}

.option-list-div:after {
  content: "";
  width: 0.8em;
  height: 0.5em;
  background-color: var(--select-arrow);
  clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  justify-self: end;
}

.option-list-root-div {
  margin-bottom: 0.6rem;
  font-size: 0.9rem;
  color: rgb(65, 65, 65);
  text-transform: capitalize;
  margin-left: 0;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  justify-content: space-between;
}

.option-list-desc {
  padding-right: 6px;
}

.item-option {
  text-align: center;
  text-transform: capitalize;
}

.item-head_desc {
  text-align: left;
  flex: 1;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  padding-left: 16px;
  padding-right: 16px;
}

.head_desc-name {
  font-weight: 600;
  font-size: 1.4rem;
  text-transform: capitalize;
}

.head_desc-info {
  font-size: 0.8rem;
  color: #a0a0a0;
}

.item-calories-div {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  margin-left: 0;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

.price {
  font-size: 1.1rem;
  font-weight: 500;
  line-height: 30px;
  padding-left: 16px;
}

.item-add-button-div {
  display: flex;
  justify-content: space-between;
}