.payment-button {
    background: #000;
    color: #fff;
    width: 240px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.5rem;
    border: none;
    font-size: 1rem;
    border-bottom-right-radius: 25px;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 0px;
    cursor: pointer;
    margin-top: 2.5rem;
    transition: 0.5s;
    margin-right: 0;
    margin-left: auto;
}

.payment-button:hover {
    border-bottom-left-radius: 0px;
    border-top-right-radius: 25px;
}

h1 {
    margin-left: 10px;
    margin-top: 100px;
    margin-bottom: 50px;
}

.user-payment-form {
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.user-payment-form input[type=text], .user-payment-form select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-weight: 400;
    font-size: 11pt;
    font-family: 'Montserrat', sans-serif;
}

.user-payment-form-names,
.user-payment-form-email,
.user-payment-form-address {
    display: flex;
    gap: 10px;
}

.user-payment-form-address-street-name {
    flex: 3.8 1 0;
}

.user-payment-form-address-street-number {
    flex: 1 1 0;
}

.user-payment-form-address-post-code {
    flex: 1 1 0;
}

.user-payment-form-address-city {
    flex: 1 1 0;
}

.user-payment-form-description {
    font-size: 12pt;
    padding-left: 3px;
    margin-top: 1rem;
    font-family: 'Montserrat', sans-serif;
}